import React, { createContext, useState, useContext, ReactNode, useEffect } from 'react'

export type ViewMode = 'pagination' | 'infinite'

interface ViewModeContextProps {
  viewMode: ViewMode
  toggleViewMode: () => void
}

const ViewModeContext = createContext<ViewModeContextProps>({
  viewMode: 'pagination',
  toggleViewMode: () => {},
})

export const ViewModeProvider = ({ children }: { children: ReactNode }) => {
  //  'pagination' 
  const [viewMode, setViewMode] = useState<ViewMode>('pagination')

  //       
  useEffect(() => {
    const savedMode = localStorage.getItem('viewMode') as ViewMode
    if (savedMode) {
      setViewMode(savedMode)
    }
  }, [])

  const toggleViewMode = () => {
    const newMode = viewMode === 'pagination' ? 'infinite' : 'pagination'
    setViewMode(newMode)
    localStorage.setItem('viewMode', newMode)
    
    //      
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }

  return (
    <ViewModeContext.Provider value={{ viewMode, toggleViewMode }}>
      {children}
    </ViewModeContext.Provider>
  )
}

export const useViewMode = () => useContext(ViewModeContext)