import React, { FunctionComponent } from 'react'
import styled from '@emotion/styled'
import { Link } from 'gatsby'

// https://codepen.io/JFarrow/pen/wvoRObL
const BlogNameLink = styled(Link)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 5px;
  width: 400px;
  height: 60px;
  margin: auto;

  @media (min-width: 1080px) {
    padding-left: 10%;
  }
  @media (max-width: 800px) {
    width: 200px;
  }
`
const BlogNameWrapper = styled.div`
  // font-family: 'Catamaran', sans-serif;
  color: rgb(15, 90, 79, 1);
  cursor: pointer;
  position: relative;

  &:hover {
    & > div:nth-of-type(2) {
      clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 100% 100%);
    }
    & > div:nth-of-type(1) {
      transform: translateY(-9em);
      @media (max-width: 800px) {
        transform: translateY(-7em);
      }
    }
    & > h1:nth-of-type(1) > div:nth-of-type(1) {
      transform: translateY(-4em);
    }
  }
`

const DefaultName = styled.h1`
  position: relative;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-transform: uppercase;
  font-size: 6em;
  -webkit-text-stroke: 1px rgb(15, 90, 79, 1);
  // -webkit-text-fill-color: rgba(255, 255, 255, 0.6);
  @media (max-width: 800px) {
    font-size: 5em;
  }
`

const HighlightLine = styled.div`
  position: absolute;
  width: 100%;
  bottom: -50;
  left: 0;
  height: 50px;
  background-color: rgb(15, 90, 79, 0.5);
  z-index: -1;
  transition: transform 400ms ease;
`

const UnderLine = styled.div`
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0.3em;
  height: 3px;
  background-color: rgb(15, 90, 79, 1);
  z-index: 5;
  transition: transform 580ms cubic-bezier(0.2, 0.1, 0.15, 1.32);
`

const Filled = styled.div`
  position: absolute;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 6em;
  top: 0;
  background-color: rgb(15, 90, 79, 0.5);
  clip-path: polygon(0% 100%, 0% 99%, 100% 99%, 100% 100%);
  transition: clip-path 600ms cubic-bezier(0.2, 0.1, 0.15, 1.32);
`

const BlogName: FunctionComponent = function () {
  return (
    <BlogNameLink to={'/'}>
      <BlogNameWrapper>
        <DefaultName>
          Ian's Archive 
          <HighlightLine />
        </DefaultName>
        <UnderLine />
        <Filled />
      </BlogNameWrapper>
    </BlogNameLink>
  )
}

export default BlogName
