import React, { useState, useMemo } from 'react'
import styled from '@emotion/styled'
import Tab from './Tab'
import BlogName from './BlogName'
import SearchBar from './SearchBar'
import Sidebar from './Sidebar'
import SearchModal from './SearchModal'
import { useStaticQuery, graphql } from 'gatsby'
import { FaSearch, FaTimes } from 'react-icons/fa'

const HeaderWrapper = styled.header`
  display: block;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 60px;
  left: 50%;
  z-index: 300;
  position: fixed;
  transform: translateX(-50%);
  background: var(--header-bg);
  backdrop-filter: var(--glass-blur); 
  background-color: var(--header-bg); 
  -webkit-backdrop-filter: var(--glass-blur);
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: center; /*     */

  @media (min-width: 1280px) {
    width: 1280px;
  }
  @media (min-width: 600px) {
    padding-left: 24px;
    padding-right: 24px;
  }
`

const HeaderContentWrapper = styled.div`
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 20px;
  
  @media (min-width: 768px) {
    padding: 0 40px;
    max-width: 768px;
  }
  @media (min-width: 1024px) {
    max-width: 1024px;
  }
  @media (min-width: 1280px) {
    max-width: 1280px;
  }
  
  display: flex;
  align-items: center;
  justify-content: space-between;
`

const ItemWrapper = styled.div`
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%; /*    */
  display: flex;
  flex-wrap: nowrap; /* []    */
  box-sizing: border-box;
  padding: 0 10px; /*     */
`

//    
const SearchButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-main);
  width: 60px; /*     */
  height: 60px;

  &:hover {
    color: var(--primary-color);
  }

  svg {
    width: 28px;
    height: 28px;
    fill: currentColor;
  }
`

//      
const BlogNameWrapper = styled.div`
  flex: 1; /*    */
  display: flex;
  justify-content: center; /*   */
  white-space: nowrap; /*   */
  overflow: hidden; /*    */
  text-overflow: ellipsis; /* ...  */
  
  /*           */
  /* : & > h1 { font-size: 1.2rem; } */
`

//     (Overlay)
const SearchOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.98);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
`

const CloseButton = styled.div`
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  cursor: pointer;
  color: #333;
`

const MainHeader = () => {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false)
  const [isSearchOpen, setIsSearchOpen] = useState(false)

  const data = useStaticQuery(graphql`
    query HeaderQuery {
      site {
        siteMetadata {
          title
          description
          author
          location
          comment
          github
          email
          siteUrl
        }
      }
      file(name: { eq: "profile" }) {
        childImageSharp {
          gatsbyImageData(width: 120, height: 120, placeholder: BLURRED, layout: FIXED)
        }
      }
      allMarkdownRemark(
        sort: [
          { frontmatter: { date: DESC } }
          { frontmatter: { title: DESC } }
        ]
      ) {
        edges {
          node {
            frontmatter {
              title
              category
            }
            fields {
              slug
            }
          }
        }
        group(field: frontmatter___category) {
          fieldValue
          totalCount
        }
      }
    }
  `)

  const searchList = data.allMarkdownRemark.edges.map((value: any) => {
    return {
      title: value.node.frontmatter.title,
      category: value.node.frontmatter.category,
      slug: value.node.fields.slug,
    }
  })

  const categoryList = useMemo(() => {
    const list: { [key: string]: number } = {}
    data.allMarkdownRemark.group.forEach((item: any) => {
      list[item.fieldValue] = item.totalCount
    })
    return list
  }, [data])

  const toggleSidebar = () => {
    setIsSidebarOpen(!isSidebarOpen)
  }

  const toggleSearch = () => {
    setIsSearchOpen(!isSearchOpen)
  }

  const profileImage = data.file?.childImageSharp?.gatsbyImageData

  return (
    <>
      <HeaderWrapper>
        <HeaderContentWrapper>
          <ItemWrapper>
            {/* :   */}
            <Tab active={isSidebarOpen} onClick={toggleSidebar} />
            
            {/* :   */}
            <BlogNameWrapper><BlogName /></BlogNameWrapper>
            {/* :     (SearchModal ) */}
            <SearchButton onClick={toggleSearch} aria-label="Search">
              <svg viewBox="0 0 24 24">
                <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
              </svg>
            </SearchButton>
          </ItemWrapper>
        </HeaderContentWrapper>
      </HeaderWrapper>

      {/*  */}
      <Sidebar
        isOpen={isSidebarOpen}
        onClose={() => setIsSidebarOpen(false)}
        categoryList={categoryList}
        siteMetadata={data.site.siteMetadata}
        profileImage={profileImage}
      />

      <SearchModal 
        isOpen={isSearchOpen} 
        onClose={() => setIsSearchOpen(false)} 
        posts={searchList} 
      />
    </>
  )
}

export default MainHeader